@media only screen and (min-width: 768px) { .pb-md-5 { padding-bottom: 48px; } }

@media only screen and (min-width: 992px) { .pb-lg-0 { padding-bottom: 0; } }

@media only screen and (min-width: 1150px) { .pl-xl-5 { padding-left: 48px; } }

