body { font-family: 'Quicksand', sans-serif; color: #fff; background: #000; font-size: 20px; line-height: 1.6; overflow-x: hidden; }

a { color: #fff; }

a:hover { color: #fff; }

.centered { max-width: 1150px; padding-left: 20px; padding-right: 20px; margin-left: auto; margin-right: auto; }

.bg-danger { background: #de0399; }

.bg-secondary { background: #3d5770; }

