.text-danger { color: #de0399; }

.text-center { text-align: center; }

.text-uppercase { text-transform: uppercase; }

.text-right { text-align: right; }

h1, h2, h3 { font-family: 'LT Energy VF'; }

h1 { font-size: 72px; line-height: 80px; }

h2 { font-size: 52px; line-height: 58px; margin-bottom: 15px; }

h3 { font-size: 22px; line-height: 28px; }

p { margin-bottom: 23px; }

.item { position: relative; display: inline-block; }

.item img:not(.capsule) { clip-path: polygon(0 0, 100% 0, 100% 100%, 10% 100%); }

.item .capsule_holder { position: absolute; bottom: -76px; right: -30px; width: 41%; }

.item:after { content: ""; width: 100%; height: 100%; background: #de0399; position: absolute; top: 20px; left: -20px; z-index: -1; clip-path: polygon(0 0, 100% 0, 100% 100%, 10% 100%); }

.img-fluid { display: inline-block; max-width: 100%; height: auto; }

.btn { background: #de0399; font-size: 20px; font-weight: bold; line-height: 1.5; color: #fff; display: inline-block; min-width: 180px; text-align: center; text-decoration: none; vertical-align: middle; cursor: pointer; user-select: none; outline: none; border: 0; cursor: pointer; padding: 14px 30px 14px 15px; font-weight: bold; position: relative; transition: all 0.3s ease; clip-path: polygon(0 0, 93% 4%, 100% 100%, 0% 100%); }

.btn span, .btn svg { position: relative; z-index: 1; }

.btn:before { content: ""; position: absolute; left: 0; top: 0; width: 100%; height: 100%; background-color: #fff; clip-path: circle(var(--size) at var(--pos)); transition: clip-path 0.3s linear; }

.btn:hover { text-decoration: none; color: #de0399; }

.btn:hover:before { --size: 150%; }

.btn.at_left { clip-path: polygon(0 0, 100% 0, 100% 100%, 7% 100%); padding: 14px 15px 14px 30px; }

.btn svg { width: 32px; }

.header { padding-top: 70px; padding-bottom: 20px; }

.header .h_nav { margin: 0; padding: 0; list-style: none; }

.header .h_nav li { padding-left: 20px; }

.header .h_nav li a { font-size: 20px; display: block; padding: 5px; position: relative; }

.header .h_nav li a span { position: relative; z-index: 1; }

.article_box { padding-top: 121px; padding-bottom: 150px; }

.article_box .centered { position: relative; }

.article_box .btn_line { padding-top: 13px; }

.article_box .article_box_txt { max-width: 65%; flex: 0 0 65%; position: relative; z-index: 1; }

@media only screen and (max-width: 1150px) { .article_box .article_box_txt { background: red; } }

.article_box .article_box_img { position: absolute; pointer-events: none; top: 50%; right: -7%; transform: translateY(-50%); width: 54%; }

.items_section_one { padding-top: 50px; padding-bottom: 50px; }

@media only screen and (max-width: 768px) { .items_section_one { justify-content: center; } }

.col_txt { max-width: 53%; flex: 0 0 53%; padding-right: 15px; padding-left: 15px; }

@media only screen and (max-width: 768px) { .col_txt { max-width: 100%; flex: 0 0 100%; } }

.col_img { max-width: 44%; flex: 0 0 44%; padding-left: 20px; }

@media only screen and (max-width: 768px) { .col_img { flex: 0 0 auto; width: auto; max-width: 100%; } }

.road_map_section { padding-top: 100px; }

.road_list { padding-left: 60px; padding-right: 60px; }

.road_item { overflow: hidden; }

.road_item_wrapper { max-width: 52%; flex: 0 0 52%; position: relative; }

.road_item_content { padding: 36px 30px 50px 50px; clip-path: polygon(15px 0, 100% 0, 100% 100%, 0 100%); }

.road_item_content h3 { font-size: 36px; line-height: 39px; }

.road_item_content p { font-size: 18px; line-height: 30px; margin-bottom: 0; }

.road_item { display: flex; padding-bottom: 80px; }

.road_item:nth-of-type(even) { justify-content: flex-end; }

.road_item:nth-of-type(even) .road_item_content { clip-path: polygon(0 0, calc(100% - 15px) 0, 100% 100%, 0 100%); }

.road_item:nth-of-type(even) .anim_elements { left: auto; right: 100%; }

.road_item:last-of-type { padding-bottom: 0; }

.road_item:last-of-type .anim_elements { display: none !important; }

.faq_section { padding-top: 100px; padding-bottom: 60px; }

.faq_list { padding-left: 26px; padding-right: 26px; }

.faq_item { padding-bottom: 40px; }

.faq_item_header { font-size: 24px; line-height: 28px; margin-bottom: 0; font-weight: bold; cursor: pointer; min-height: 80px; padding: 10px 40px 10px 50px; user-select: none; }

.faq_item_header p { margin: 0; padding-right: 15px; }

.switcher { max-width: 26px; flex: 0 0 26px; height: 26px; border: 2px solid #fff; border-radius: 50%; margin-left: auto; position: relative; }

.switcher:before, .switcher:after { content: ""; height: 2px; width: 54%; background: #fff; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); transition: transform 0.3s ease; }

.switcher:after { width: 2px; height: 54%; }

.switcher.opened:after { transform: translate(-50%, -50%) rotate(-90deg); }

.faq_item_content { border: 1px solid #fff; border-top: 0; padding: 40px 50px 10px; display: none; }

.faq_item_content p { font-size: 18px; line-height: 30px; font-weight: bold; }

.our_team_section { padding-top: 100px; }

.owl_section { padding-top: 20px; }

.owl_item h3 { font-size: 32px; line-height: 36px; margin-top: 14px; }

.anim_elements { position: absolute; width: 50%; height: 100%; min-height: 166px; top: 50%; left: 100%; }

.horizontal { height: 1px; width: 100%; background: #de0399; position: absolute; top: 0; left: 0; }

.vertical { width: 1px; height: 100%; background: #de0399; top: 0; right: 0; position: absolute; }

.dot { width: 44px; height: 44px; border-radius: 50%; position: absolute; top: 0; left: 100%; transform: translate(-50%, -50%); padding: 12px; }

.dot em { display: block; height: 100%; width: 100%; border-radius: 50%; }

.road_item:nth-of-type(even) .anim_elements { transform: scale(-1, 1); }

.footer { background: #3d5770; padding-top: 40px; padding-bottom: 40px; }

.footer .copy { font-size: 20px; line-height: 24px; font-weight: bold; }

.footer .soc_list { padding: 0; margin: 0; list-style: none; }

.footer .soc_list li { margin-left: 40px; }

.footer .soc_list li svg { width: 16px; display: block; }

.footer .soc_list li svg.inst_ico { width: 23px; }

.footer .soc_list li svg.ico_f { width: 15px; }

.footer .soc_list li svg.ico_discord { width: 32px; }

.footer .soc_list li a { transition: all 0.3s ease; }

.footer .soc_list li a:hover { color: #de0399; }
